import {GraphActionStatus, Cursor, TargetDirection} from '../shared/enum'
import {NodeErwin} from '../../core/materials/node'
import {NodeRect} from '../../core/materials/node'
import {LineErwin} from '../../core/materials/line'
import {TextBase} from '../../core/materials/text'
import type {Erg} from '../index'
import {cloneDeep} from 'lodash-es'
import {
  NodeEvent,
  MarkEvent,
  NoteEvent,
  GraphEvent,
  LineEvent,
  NodeAttrEvent,
} from '../../types/event'
import {uuid} from '../../core/utils/uuid'
import {NodeData} from '../types/graph'
import {warn} from '../../core/utils/log'
import {orderRun} from '../../core/utils/fn'

/**
 * 
 */
const ifAnchor = (target: unknown, canvasEv) => {
  const ifLegalMaterials =
    target instanceof NodeErwin || target instanceof NodeRect
  if (!ifLegalMaterials) return false

  // 
  const {x, width} = target
  let {y, height} = target
  if (target instanceof NodeErwin && !target.ifSubEntity) {
    y += target.titleHeight
    height -= target.titleHeight
  }
  const middleX = x + width / 2
  const middleY = y + height / 2
  const fullX = x + width
  const fullY = y + height
  const tolerance = 5
  let targetDirection = TargetDirection.NONE

  if (
    Math.abs(x - canvasEv.x) <= tolerance &&
    Math.abs(y - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.LT
  else if (
    Math.abs(middleX - canvasEv.x) <= tolerance &&
    Math.abs(y - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.TM
  else if (
    Math.abs(fullX - canvasEv.x) <= tolerance &&
    Math.abs(y - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.RT
  else if (
    Math.abs(x - canvasEv.x) <= tolerance &&
    Math.abs(middleY - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.LM
  else if (
    Math.abs(fullX - canvasEv.x) <= tolerance &&
    Math.abs(middleY - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.RM
  else if (
    Math.abs(x - canvasEv.x) <= tolerance &&
    Math.abs(fullY - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.LB
  else if (
    Math.abs(middleX - canvasEv.x) <= tolerance &&
    Math.abs(fullY - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.BM
  else if (
    Math.abs(fullX - canvasEv.x) <= tolerance &&
    Math.abs(fullY - canvasEv.y) <= tolerance
  )
    targetDirection = TargetDirection.RB

  return targetDirection
}

export const getTargetAttributeInfo = ({
  sourceNodeId,
  sourceAttrIds,
  targetNode,
  canvasEv,
}) => {
  const targetPointInfo = targetNode?.containsPoint(canvasEv)
  const ifHoverNode = targetNode instanceof NodeErwin
  const ifLegalAttr = targetPointInfo?.type === 'attribute'
  if (!ifHoverNode || !ifLegalAttr) return
  const attrId = targetPointInfo.id

  const ifPk = !!+targetNode.getAttributeInfo(attrId).info.ifPk

  const ifVirtualAttr = ['PrimaryKey', 'NonPrimaryKey'].includes(
    attrId as string,
  )
  const targetAttrs = ifPk
    ? targetNode.attributesPrimary
    : targetNode.attributesPrimaryNon
  const beforeAttrIdx = ifVirtualAttr
    ? targetAttrs.length - 1
    : targetAttrs.findIndex((it) => it.id === attrId) - 1
  const beforeAttrId = targetAttrs[beforeAttrIdx]?.id ?? null
  const options = {
    sourceNodeId,
    sourceAttrIds,
    targetNodeId: targetNode.id,
    afterAttrId: ifVirtualAttr ? null : attrId,
    beforeAttrId,
    ifPk,
  }

  return options
}

export const getTargetEvent = (hoverTarget) =>
  hoverTarget instanceof NodeErwin
    ? NodeEvent
    : hoverTarget instanceof LineErwin
    ? LineEvent
    : hoverTarget instanceof NodeRect
    ? MarkEvent
    : hoverTarget instanceof TextBase
    ? NoteEvent
    : null

export const useMouseMove = function (this: Erg, e: MouseEvent, canvasEv) {
  /**
   * 
   */
  const moveMarkRect = () => {
    const {event} = this
    const offsetOfMove = {
      x: canvasEv.x - event.targetOffsetX - event.target.x,
      y: canvasEv.y - event.targetOffsetY - event.target.y,
    }
    const newLoc = {
      x: event.target.x + offsetOfMove.x,
      y: event.target.y + offsetOfMove.y,
    }

    this.dataHandler.markMap.get(event.target.id).updateOptions(newLoc)
    this.emit(MarkEvent.Move, this.dataHandler.markMap.get(event.target.id))
    this.update()
  }

  /**
   * 
   */
  const moveMarkText = () => {
    const {event} = this

    const offsetOfMove = {
      x: canvasEv.x - event.targetOffsetX - event.target.x,
      y: canvasEv.y - event.targetOffsetY - event.target.y,
    }

    const newLoc = {
      x: event.target.x + offsetOfMove.x,
      y: event.target.y + offsetOfMove.y,
    }
    this.dataHandler.noteMap.get(event.target.id).updateOptions(newLoc)

    this.emit(NoteEvent.Move, event.target)
    this.update()
  }

  /**
   * 
   */
  const resizeNode = () => {
    const {event} = this
    // event.target.manualChange = true
    const {x, y} = canvasEv
    if (event.target.type === 'mark') {
      this.resizeNode(x, y, event, null)
    } else {
      this.resizeNode(x, y, event)
    }

    this.update()
  }

  /**
   * 
   */
  const moveNode = () => {
    const {
      focusTarget,
      event,
      dataHandler: {nodeRelatedLineMap},
    } = this

    const offsetOfMove = {
      x: canvasEv.x - event.targetOffsetX - event.target.x,
      y: canvasEv.y - event.targetOffsetY - event.target.y,
    }

    focusTarget.forEach((focusItem) => {
      if (!(focusItem instanceof NodeErwin)) return

      const nodeNewLoc = {
        x: focusItem.x + offsetOfMove.x,
        y: focusItem.y + offsetOfMove.y,
      }

      // 
      if (nodeRelatedLineMap.has(focusItem.id)) {
        const involveLines = [
          ...nodeRelatedLineMap.get(focusItem.id).from,
          ...nodeRelatedLineMap.get(focusItem.id).to,
        ]
        involveLines.forEach((line) => {
          line.from === line.to
            ? this.moveLineForSameNode(line, {target: focusItem})
            : this.moveLineForNode(line, {target: focusItem}, nodeNewLoc)
        })
      }

      this.dataHandler.nodeMap.get(focusItem.id).updateOptions(nodeNewLoc)
      this.emit(NodeEvent.Move, focusItem)
    })

    this.update()
  }

  /**
   * 
   */
  const moveAttr = () => {
    const {event, focusAttrs, hoverTarget} = this
    changeMouseCursor()
    const options = getTargetAttributeInfo({
      sourceNodeId: event.target.id,
      sourceAttrIds: [...focusAttrs],
      targetNode: hoverTarget,
      canvasEv,
    })
    options && this.emit(NodeAttrEvent.Drag, e, options)
  }

  /**
   * 
   */
  const moveLine = () => {
    const {event} = this

    const idx = event.target.getIndexInPos(event.lastEvLoc)
    event.targetLineStartPointIdx = idx
    event.lastEvLoc = canvasEv
    if (idx === false) return
    this.moveLineForLine(event, canvasEv)
    this.update()
  }

  /**
   * 
   */
  const moveCanvas = () => {
    this.canvas.setTranslate(e)
    this.canvas.clearEventCanvas()
    const graphTranslate = this.canvas.getTranslate()
    this.emit(GraphEvent.Move, graphTranslate)
    this.update()
  }

  /**
   * 
   */
  const changeMouseCursor = () => {
    const {status, renderElements} = this

    const cursorMap = {
      [GraphActionStatus.AddNode]: Cursor.cell,
      [GraphActionStatus.AddPrimaryKey]: Cursor.crosshair,
      [GraphActionStatus.AddForeignKey]: Cursor.crosshair,
      [GraphActionStatus.AddFiltered]: Cursor.crosshair,
      [GraphActionStatus.AddMark]: Cursor.cell,
      [GraphActionStatus.AddNote]: Cursor.cell,
      [GraphActionStatus.Copy]: Cursor.copy,
    }

    const cursorState = cursorMap[status]
    // 
    // if (cursorState)
    //   return (this.canvas.eventEl.style.cursor = cursorState || Cursor.default)

    let hoverTarget = null,
      cursor = Cursor.grab

    // 
    for (let i = renderElements.length - 1; i >= 0; i--) {
      const el = renderElements[i]
      const isTarget = ifAnchor(el, canvasEv) || el.containsPoint(canvasEv).type
      if (isTarget) {
        hoverTarget = el

        cursor =
          isTarget === TargetDirection.LT
            ? Cursor.nwseResize
            : isTarget === TargetDirection.TM
            ? Cursor.nsResize
            : isTarget === TargetDirection.RT
            ? Cursor.neswResize
            : isTarget === TargetDirection.LM
            ? Cursor.ewResize
            : isTarget === TargetDirection.RM
            ? Cursor.ewResize
            : isTarget === TargetDirection.LB
            ? Cursor.neswResize
            : isTarget === TargetDirection.BM
            ? Cursor.nsResize
            : isTarget === TargetDirection.RB
            ? Cursor.nwseResize
            : Cursor.move
        break
      }
    }

    // 
    if (this.hoverTarget !== hoverTarget) {
      const OldTargetEvent =
        this.hoverTarget instanceof NodeErwin
          ? NodeEvent
          : this.hoverTarget instanceof LineErwin
          ? LineEvent
          : this.hoverTarget instanceof NodeRect
          ? MarkEvent
          : this.hoverTarget instanceof TextBase
          ? NoteEvent
          : null
      const NewTargetEvent =
        hoverTarget instanceof NodeErwin
          ? NodeEvent
          : hoverTarget instanceof LineErwin
          ? LineEvent
          : hoverTarget instanceof NodeRect
          ? MarkEvent
          : hoverTarget instanceof TextBase
          ? NoteEvent
          : null
      OldTargetEvent && this.emit(OldTargetEvent.Leave, this.hoverTarget)
      NewTargetEvent && this.emit(NewTargetEvent.Enter, hoverTarget)
    }

    // 
    this.hoverTarget = hoverTarget
    this.canvas.eventEl.style.cursor = cursorState ?? cursor
    this.updateEventCanvas()
  }

  /**
   * 
   */
  const moveElement = () => {
    const {event, focusAttrs} = this

    const isAttrMove = event.target && !!focusAttrs.size
    const isNodeResize = !!event.targetDirection
    const isNodeMove = event.target instanceof NodeErwin
    const isLineMove = event.target instanceof LineErwin
    const isMarkMove = event.target instanceof NodeRect
    const isNoteMove = event.target instanceof TextBase

    if (isAttrMove) return moveAttr()
    if (isNodeResize) return resizeNode()
    if (isNodeMove) return moveNode()
    if (isLineMove) return moveLine()
    if (isMarkMove) return moveMarkRect()
    if (isNoteMove) return moveMarkText()
  }

  return {
    changeMouseCursor,
    moveCanvas,
    moveElement,
  }
}

let _clickTimer
export const useMouseDown = function (this: Erg, e, canvasEv) {
  const {x: evX, y: evY} = canvasEv

  const clearFocusTarget = (keepFocus?) => {
    this.focusTarget.forEach((it) => {
      if (it === keepFocus) return
      this.emit(getTargetEvent(it).Blur, it)
      this.focusTarget.delete(it)
    })
    return this
  }

  const execTask = () => {
    const {status, defaultWidth, defaultHeight} = this
    if (!status) return false
    switch (status) {
      case GraphActionStatus.Copy: {
        const focusNodes = cloneDeep(
          [...this.focusTarget].filter((it) => !(it instanceof LineErwin)),
        ) as NodeErwin[]
        if (!focusNodes.length) break

        const distance = {
          x: evX - focusNodes[0].x,
          y: evY - focusNodes[0].y,
        }

        const fixNodes = focusNodes.map((node) => {
          node.x = node.x + distance.x
          node.y = node.y + distance.y
          delete node.id
          return node
        })
        this.emit(GraphEvent.Paste, fixNodes)
        this.status = GraphActionStatus.None
        this.canvas.eventEl.style.cursor = Cursor.default
        this.update()
        break
      }
      case GraphActionStatus.AddNode: {
        const newNode = {
          x: evX - defaultWidth / 2,
          y: evY - defaultHeight / 2,
          width: defaultWidth,
          height: defaultHeight,
        }

        /**
         * beforeCreateResults
         *  - []: 
         *  - promisepromise
         *    - rejectreject
         *    - resolve
         */
        const beforeCreateResults = this.emit(NodeEvent.BeforeCreate, newNode)
        Promise.all(beforeCreateResults)
          .then(() => {
            this.emit(NodeEvent.New, newNode)
            this.update()
          })
          .catch(() => warn('CreateNode abort in [BeforeCreate] hooks.'))

        this.status = GraphActionStatus.None
        this.canvas.eventEl.style.cursor = Cursor.default
        break
      }
      case GraphActionStatus.AddMark: {
        const newMark = {
          id: uuid(),
          x: evX,
          y: evY,
          width: defaultWidth,
          height: defaultHeight,
          type: 'mark',
        }

        this.emit(MarkEvent.New, newMark)
        this.status = GraphActionStatus.None
        this.canvas.eventEl.style.cursor = Cursor.default
        this.update()
        break
      }
      case GraphActionStatus.AddNote: {
        const newNote = {
          id: uuid(),
          x: evX,
          y: evY,
          text: '',
          type: 'note',
        }

        this.emit(NoteEvent.New, newNote)
        this.status = GraphActionStatus.None
        this.canvas.eventEl.style.cursor = Cursor.default
        this.update()
        break
      }
      // case GraphActionStatus.AddPrimaryKey:
      //   break
      // case GraphActionStatus.AddForeignKey:
      //   break
      // case GraphActionStatus.AddFiltered:
      //   break

      // AddPrimaryKey | AddForeignKey | AddFiltered
      default:
        if (this.hoverTarget instanceof NodeErwin) {
          this.chooseTargets.push(
            this.dataHandler.originDataMap.get(this.hoverTarget.id) as NodeData,
          )
        }
        if (this.chooseTargets.length > 1) {
          this.status === GraphActionStatus.AddFiltered
            ? this.createBranchLine()
            : this.createLine(this.status)
          this.status = GraphActionStatus.None
          this.chooseTargets = []
        }
        break
    }
  }

  const execEntityRename = async () => {
    const {canvas, hoverTarget, focusTarget} = this
    const ifFocusEntity =
      hoverTarget instanceof NodeErwin && focusTarget.has(hoverTarget)
    if (!ifFocusEntity) return false

    const {x, y} = this.canvas.transMousePositionForCanvas(e)
    const ifTitle = hoverTarget?.containsPoint({x, y}).type === 'title'
    if (!ifTitle) return false

    const nameKey = this.ifLogic ? 'cnName' : 'enName'
    const options = {
      type: 'node-name',
      key: nameKey,
      value: (hoverTarget as NodeErwin)[nameKey],
      entityId: hoverTarget.id,
      getLocation() {
        const {x, y} = canvas.transCanvasPositionToOrigin({
          x: (hoverTarget as NodeErwin).x,
          y: (hoverTarget as NodeErwin).y + 4,
        })
        return {
          width: (hoverTarget as NodeErwin).width,
          height: (hoverTarget as NodeErwin).attrHeight,
          x,
          y,
        }
      },
    }

    if (_clickTimer) {
      clearTimeout(_clickTimer)
      _clickTimer = null
      warn('dblClick')
    } else {
      _clickTimer = setTimeout(() => {
        _clickTimer = null
        this.emit(NodeEvent.Rename, options)
      }, 250)
    }
    // waiting cursor change
    // await sleep(250)
    return true
  }

  const execAttrRename = async () => {
    const {canvas, hoverTarget, focusAttrs} = this
    const targetPointInfo = hoverTarget.containsPoint(canvasEv)
    const ifAttrRename = focusAttrs.has(targetPointInfo.id)
    if (!ifAttrRename) return false

    const focusAttrId = targetPointInfo.id
    const attrInfo = (hoverTarget as NodeErwin).getAttributeInfo(focusAttrId)
    const nameKey = this.ifLogic ? 'cnName' : 'enName'
    const options = {
      type: 'node-attr',
      key: nameKey,
      value: attrInfo.info[nameKey],
      entityId: hoverTarget.id,
      attrId: attrInfo.info.id,
      originData: attrInfo.info,
      getLocation() {
        const attrInfo = (hoverTarget as NodeErwin).getAttributeInfo(
          focusAttrId,
        )
        const {x, y} = canvas.transCanvasPositionToOrigin({
          x: attrInfo.x,
          y: attrInfo.y - 1,
        })
        return {
          width: (hoverTarget as NodeErwin).width - attrInfo.padding * 2,
          height: (hoverTarget as NodeErwin).attrHeight,
          x,
          y,
        }
      },
    }

    if (_clickTimer) {
      clearTimeout(_clickTimer)
      _clickTimer = null
      warn('dblClick')
    } else {
      _clickTimer = setTimeout(() => {
        _clickTimer = null
        this.emit(NodeAttrEvent.Rename, options)
      }, 250)
    }
    // await sleep(250)
    return true
  }

  const execAttrFocus = () => {
    const {hoverTarget, focusAttrs, focusTarget} = this
    const ifMultiSelect = e.metaKey || e.ctrlKey
    const ifFocusTarget = focusTarget.has(hoverTarget)

    // 
    const targetDirection = ifAnchor(hoverTarget, canvasEv)
    const targetPointInfo = hoverTarget.containsPoint(canvasEv)
    const ifAttr = targetPointInfo?.type === 'attribute'
    const ifVirtualAttr =
      ifAttr &&
      ['PrimaryKey', 'NonPrimaryKey'].includes(targetPointInfo?.id as string)
    const ifAttrFocus =
      ifAttr &&
      hoverTarget instanceof NodeErwin &&
      ifFocusTarget &&
      !targetDirection &&
      !ifVirtualAttr

    if (!ifMultiSelect) focusAttrs.clear()
    if (ifAttrFocus) {
      focusAttrs.add(targetPointInfo.id)
      return true
    }
  }

  const execMaterialsFocus = () => {
    const {hoverTarget, event, focusTarget} = this
    const ifMultiSelect = e.metaKey || e.ctrlKey
    const ifFocusTarget = focusTarget.has(hoverTarget)
    const TargetEvent = getTargetEvent(hoverTarget)
    const targetDirection = ifAnchor(hoverTarget, canvasEv)

    //  |  -> 
    const ifContext = ifFocusTarget && e.button === 2
    const ifLine = TargetEvent === LineEvent
    const ifClearFocusTarget = !(ifMultiSelect || ifContext) || ifLine
    if (ifClearFocusTarget) clearFocusTarget(hoverTarget)

    // 
    if (hoverTarget instanceof LineErwin) {
      event.lastEvLoc = canvasEv
    } else if (
      hoverTarget instanceof NodeErwin ||
      hoverTarget instanceof TextBase ||
      hoverTarget instanceof NodeRect
    ) {
      event.targetOffsetX = canvasEv.x - hoverTarget.x
      event.targetOffsetY = canvasEv.y - hoverTarget.y
      event.targetDirection = targetDirection || null
    }

    // hover
    if (!focusTarget.has(hoverTarget)) {
      this.emit(TargetEvent.Focus, hoverTarget)
      focusTarget.add(hoverTarget)

      this.updateEventCanvas()
      return true
    }
  }

  const setEventTarget = () => {
    const {hoverTarget, event} = this

    event.target = hoverTarget
    this.canvas.eventEl.style.cursor = Cursor.grabbing

    // hover
    const ifCanvasTarget = !hoverTarget
    if (ifCanvasTarget) return clearFocusTarget().updateEventCanvas()

    // 
    const TargetEvent = getTargetEvent(hoverTarget)
    this.emit(TargetEvent.MouseDown, hoverTarget)

    orderRun([
      execEntityRename,
      execAttrRename,
      execAttrFocus,
      execMaterialsFocus,
    ])
  }

  return {
    execTask,
    setEventTarget,
  }
}

export const useMouseUp = function (this: Erg, e: MouseEvent, canvasEv) {
  const {event, focusAttrs, hoverTarget} = this

  const resetMouseState = () => {
    const {hoverTarget} = this
    // 
    const TargetEvent =
      hoverTarget instanceof NodeErwin
        ? NodeEvent
        : hoverTarget instanceof LineErwin
        ? LineEvent
        : hoverTarget instanceof NodeRect
        ? MarkEvent
        : hoverTarget instanceof TextBase
        ? NoteEvent
        : null
    TargetEvent && this.emit(TargetEvent.MouseUp, hoverTarget)

    this.canvas.eventEl.style.cursor = Cursor.default
    Object.assign(this.event, {
      target: null,
      targetDirection: TargetDirection.NONE,
      targetOffsetX: 0,
      targetOffsetY: 0,
      targetLineStartPointIdx: 0,
      lastEvLoc: {x: 0, y: 0},
    })
    this.update()
  }
  const drop = () => {
    const {x: curX, y: curY} = this.canvas.transMousePositionForCanvas(e)
    const ifNotMove = curX === event.canvasEv.x && curY === event.canvasEv.y
    const ifMouseDown = !!event.target
    const ifFocusAttr = !!focusAttrs.size
    const ifMultiSelection = e.metaKey || e.ctrlKey
    const ifContext = e.button === 2
    const notDropAttr =
      ifNotMove || !ifMouseDown || !ifFocusAttr || ifMultiSelection || ifContext
    if (notDropAttr) return false

    // drop attrs
    const options = getTargetAttributeInfo({
      sourceNodeId: event.target.id,
      sourceAttrIds: [...focusAttrs],
      targetNode: hoverTarget,
      canvasEv,
    })
    const ifSelfEmit =
      focusAttrs.size === 1 && focusAttrs.has(options.afterAttrId)
    if (ifSelfEmit) return false

    const beforeDropResults = this.emit(NodeAttrEvent.BeforeDrop, e, options)
    Promise.all(beforeDropResults)
      .then(() => {
        this.emit(NodeAttrEvent.Drop, e, options)
        focusAttrs.clear()
        this.update()
      })
      .catch((err) => warn('AttrDrop abort in BeforeDrop.', err))
  }
  return {
    resetMouseState,
    drop,
  }
}
