import type {Erg} from '..'
import {uuid, uuname} from '../../core/utils/uuid'
import {Id} from '../../types'
import {
  NodeEvent,
  LineEvent,
  MarkEvent,
  NoteEvent,
  NodeAttrEvent,
  GraphEvent,
  EditorEvent,
} from '../../types/event'
import type {
  NodeData,
  LineData,
  MarkData,
  NoteData,
  ErgData,
} from '../types/graph'
import {warn} from '../../core/utils/log'
import type {NodeErwin as Node} from '../../core/materials/node'
import type {NodeRect} from '../../core/materials/node'
import type {LineErwin} from '../../core/materials/line/line-erwin'
import type {TextBase} from '../../core/materials/text'

export interface RelatedLineItem {
  from: Set<LineErwin>
  to: Set<LineErwin>
}
interface DataHandlerOptions {
  context?: Erg
  data: ErgData
}

/**
 * 
 *  - mountAutoUpdate -> 
 *  - mountAutoUpdate -> 
 */
export class DataHandler {
  private context: Erg
  public originData: ErgData = {
    nodeDataArray: [],
    linkDataArray: [],
    markDataArray: [],
    noteDataArray: [],
  }
  public originDataMap: Map<Id, NodeData | LineData | MarkData | NoteData> =
    new Map()

  public nodeMap: Map<Id, Node> = new Map()
  public lineMap: Map<Id, LineErwin> = new Map()
  public noteMap: Map<Id, TextBase> = new Map()
  public markMap: Map<Id, NodeRect> = new Map()
  public nodeRelatedLineMap: Map<Node['id'], RelatedLineItem> = new Map()

  constructor(options: DataHandlerOptions) {
    this.update(options)
    this.mountAutoUpdate()
  }

  public update(options: DataHandlerOptions) {
    this.analysisOptions(options)
  }

  private analysisOptions(options: DataHandlerOptions) {
    const {data, context} = options
    if (data) this.originData = data
    if (context) this.context = context
    this.clearMap()
    this.initMap()
  }

  private clearMap() {
    this.originDataMap?.clear()
    this.lineMap?.clear()
    this.markMap?.clear()
    this.noteMap?.clear()
    this.nodeMap?.clear()
    this.nodeRelatedLineMap?.clear()
  }

  private initMap() {
    const {
      originData: {
        nodeDataArray = [],
        linkDataArray = [],
        markDataArray = [],
        noteDataArray = [],
      },
      originDataMap,
    } = this

    originDataMap.clear()
    const elements = [
      ...nodeDataArray,
      ...linkDataArray,
      ...markDataArray,
      ...noteDataArray,
    ]
    for (let i = 0; i < elements.length; i++) {
      const el = elements[i]
      originDataMap.set(el.id, el)
    }
  }

  private mountAutoUpdate() {
    this.mountGraphEvent()
    this.mountNodeEvent()
    this.mountNodeAttrEvent()
    this.mountLineEvent()
    this.mountMarkEvent()
    this.mountNoteEvent()
    this.mountEditorEvent()
  }

  private mountGraphEvent() {
    const {context} = this
    context.on(GraphEvent.Paste, (materials: any[]) => {
      materials.map((it) => {
        context.emit(NodeEvent.New, it)
      })
    })
  }

  private mountNodeEvent() {
    const {originDataMap, context} = this
    context.on(NodeEvent.Move, (node) => context.emit(NodeEvent.Change, node))
    context.on(NodeEvent.Resize, (node) => context.emit(NodeEvent.Change, node))
    context.on(NodeEvent.Change, (node) => {
      const it = originDataMap.get(node.id) as NodeData
      it.enName = node.enName
      it.cnName = node.cnName
      it.x = node.x
      it.y = node.y
      it.width = node.width
      it.height = node.height
      // it.color = node.color
      it.bdC = node.bdC
      it.bgC = node.bgC
    })
    context.on(
      NodeEvent.Delete,
      function (node) {
        const idx = this.originData.nodeDataArray.findIndex(
          (it) => it.id === node.id,
        )
        if (idx === -1) return

        this.originData.nodeDataArray.splice(idx, 1)
        originDataMap.delete(node.id)
      }.bind(this),
    )
    context.on(NodeEvent.New, (info) => {
      if (!info.id) info.id = uuid()
      if (!info.enName) info.enName = uuname('Entity')
      if (!info.cnName) info.cnName = uuname('')
      this.originData.nodeDataArray.push(info)
      this.originDataMap.set(info.id, info)
    })
  }

  private mountNodeAttrEvent() {
    const {originDataMap, context} = this
    context.on(NodeAttrEvent.Drop, (ev, options) => {
      const {
        sourceNodeId,
        sourceAttrIds,
        targetNodeId,
        beforeAttrId,
        afterAttrId,
        ifPk,
      } = options
      const sourceNode = originDataMap.get(sourceNodeId) as NodeData
      const targetNode = originDataMap.get(targetNodeId) as NodeData

      const removeData = []
      for (let i = 0; i < sourceAttrIds.length; i++) {
        const sourceAttrId = sourceAttrIds[i]
        const sourceAttrIndex = sourceNode.attributes.findIndex(
          (it) => it.id === sourceAttrId,
        )
        const removeAttr = sourceNode.attributes.splice(sourceAttrIndex, 1)[0]
        removeAttr.ifPk = ifPk
        removeData.push(removeAttr)
      }

      // console.log(removeData)
      if (afterAttrId) {
        const afterAttrIndex = targetNode.attributes.findIndex(
          (it) => it.id === afterAttrId,
        )
        targetNode.attributes.splice(afterAttrIndex, 0, ...removeData)
      } else {
        targetNode.attributes.push(...removeData)
      }
    })

    context.on(NodeAttrEvent.Change, (entityId, attrId, key, value) => {
      const node = originDataMap.get(entityId) as NodeData
      const attr = node.attributes.find((it) => it.id === attrId)
      attr[key] = value
    })
  }

  private mountLineEvent() {
    const {originDataMap, nodeRelatedLineMap, lineMap, context} = this
    context.on(LineEvent.Move, (line) => context.emit(LineEvent.Change, line))
    context.on(LineEvent.Change, (line) => {
      const it = originDataMap.get(line.id) as LineData
      it.pos = line.pos
    })
    context.on(
      LineEvent.Delete,
      function (line) {
        const idx = this.originData.linkDataArray.findIndex(
          (it) => it.id === line.id,
        )
        if (idx === -1) return

        const [lineInfo] = this.originData.linkDataArray.splice(idx, 1)
        nodeRelatedLineMap.get(lineInfo.from).from.delete(lineMap.get(line.id))
        nodeRelatedLineMap.get(lineInfo.to).to.delete(lineMap.get(line.id))
        lineMap.delete(line.id)
        originDataMap.delete(line.id)
      }.bind(this),
    )

    context.on(LineEvent.New, (info) => {
      if (!info.id) info.id = uuid()
      this.originData.linkDataArray.push(info)
      this.originDataMap.set(info.id, info)
    })
  }

  private mountMarkEvent() {
    const {originDataMap, context} = this
    context.on(MarkEvent.Move, (node) => context.emit(MarkEvent.Change, node))
    context.on(MarkEvent.Resize, (node) => context.emit(MarkEvent.Change, node))
    context.on(MarkEvent.Change, (node) => {
      const it = originDataMap.get(node.id) as MarkData
      it.x = node.x
      it.y = node.y
      it.width = node.width
      it.height = node.height
      // it.color = node.color
      it.bdC = node.bdC
      it.bgC = node.bgC
    })
    context.on(
      MarkEvent.Delete,
      function (mark) {
        const idx = this.originData.markDataArray.findIndex(
          (it) => it.id === mark.id,
        )
        if (idx === -1) return

        this.originData.markDataArray.splice(idx, 1)
        originDataMap.delete(mark.id)
      }.bind(this),
    )
    context.on(MarkEvent.New, (info) => {
      Object.assign(info, {
        id: uuid(),
      })
      this.originData.markDataArray.push(info)
      this.originDataMap.set(info.id, info)
    })
  }

  private mountNoteEvent() {
    const {originDataMap, context} = this
    context.on(NoteEvent.Move, (note) => context.emit(NoteEvent.Change, note))
    context.on(NoteEvent.Change, (note) => {
      const it = originDataMap.get(note.id) as NoteData
      it.x = note.x
      it.y = note.y
      it.width = note.width
      it.height = note.height
      it.color = note.color
      it.bdC = note.bdC
      it.bgC = note.bgC
    })
    context.on(
      NoteEvent.Delete,
      function (note) {
        const idx = this.originData.noteDataArray.findIndex(
          (it) => it.id === note.id,
        )
        if (idx === -1) return

        this.originData.noteDataArray.splice(idx, 1)
        originDataMap.delete(note.id)
      }.bind(this),
    )
    context.on(NoteEvent.New, (info) => {
      Object.assign(info, {
        id: uuid(),
        text: 'remark...',
      })
      this.originData.noteDataArray.push(info)
      this.originDataMap.set(info.id, info)
    })
  }

  private mountEditorEvent() {
    const {nodeMap, context} = this
    const handleChangeNodeName = (options, value) => {
      const {entityId, key} = options
      const node = nodeMap.get(entityId) as Node

      const oldValue = node[key]
      const sameValue = oldValue === value
      if (sameValue) return

      const beforeChangeResults = context.emit(
        NodeEvent.BeforeChange,
        node,
        key,
        value,
      )
      Promise.all(beforeChangeResults)
        .then(() => {
          node.updateOptions({[key]: value})
          context.emit(NodeEvent.Change, node)
        })
        .catch(() => warn('Change abort in [BeforeChange] hooks.', node))
    }
    const handleChangeAttrName = (options, value) => {
      const {entityId, attrId, key, originData} = options
      // const node = nodeMap.get(entityId) as Node

      const oldValue = originData[key]
      const sameValue = oldValue === value
      if (sameValue) return

      const beforeChangeResults = context.emit(
        NodeAttrEvent.BeforeChange,
        entityId,
        attrId,
        key,
        value,
        originData,
      )
      Promise.all(beforeChangeResults)
        .then(() => {
          // node.updateOptions({[key]: value})
          context.emit(
            NodeAttrEvent.Change,
            entityId,
            attrId,
            key,
            value,
            originData,
          )
        })
        .catch(() => warn('Change abort in [BeforeChange] hooks.', originData))
    }
    context.on(EditorEvent.Blur, (options, value) => {
      const {type} = options
      switch (type) {
        case 'node-name':
          handleChangeNodeName(options, value)
          break
        case 'node-attr':
          handleChangeAttrName(options, value)
          break

        default:
          break
      }
    })
  }

  private unmountAutoUpdate() {
    const {context} = this
    context.off()
  }

  private freeVariable() {
    this.originData = null
    this.originDataMap = null
  }

  public destroy() {
    this.unmountAutoUpdate()
    this.freeVariable()
  }
}
