  private mountCanvasEvents() {
    this._bindEventMousedown = this.onMouseDown.bind(this)
    this._bindEventMousemove = this.onMouseMove.bind(this)
    this._bindEventMouseup = this.onMouseUp.bind(this)
    this._bindEventResize = this.onResize.bind(this)
    this._bindEventWheel = this.onWheel.bind(this)
    this._bindEventContextmenu = this.onContextmenu.bind(this)
    this._bindEventUndo = this.onUndo.bind(this)
    this._bindEventRedo = this.onRedo.bind(this)
    this._bindEventKeydown = this.onKeyDown.bind(this)
    this._bindEventDblclick = this.onDblClick.bind(this)
    this._bindEventClick = this.onClick.bind(this)

    this.canvas.on(CanvasEvent.Resize, this._bindEventResize)
    this.canvas.on(CanvasEvent.Wheel, this._bindEventWheel)
    this.canvas.on(CanvasEvent.Mouseup, this._bindEventMouseup)
    this.canvas.on(CanvasEvent.Mousedown, this._bindEventMousedown)
    this.canvas.on(CanvasEvent.Mousemove, this._bindEventMousemove)
    this.canvas.on(CanvasEvent.Contextmenu, this._bindEventContextmenu)
    this.canvas.on(CanvasEvent.Undo, this._bindEventUndo)
    this.canvas.on(CanvasEvent.Redo, this._bindEventRedo)
    this.canvas.on(CanvasEvent.Keydown, this._bindEventKeydown)
    this.canvas.on(CanvasEvent.DblClick, this._bindEventDblclick)
    this.canvas.on(CanvasEvent.Click, this._bindEventClick)
  }

  private unmountCanvasEvents() {
    this.canvas.off(CanvasEvent.Resize, this._bindEventResize)
    this.canvas.off(CanvasEvent.Wheel, this._bindEventWheel)
    this.canvas.off(CanvasEvent.Mouseup, this._bindEventMouseup)
    this.canvas.off(CanvasEvent.Mousedown, this._bindEventMousedown)
    this.canvas.off(CanvasEvent.Mousemove, this._bindEventMousemove)
    this.canvas.off(CanvasEvent.Contextmenu, this._bindEventContextmenu)
    this.canvas.off(CanvasEvent.Undo, this._bindEventUndo)
    this.canvas.off(CanvasEvent.Redo, this._bindEventRedo)
    this.canvas.off(CanvasEvent.Keydown, this._bindEventKeydown)
    this.canvas.off(CanvasEvent.DblClick, this._bindEventDblclick)
    this.canvas.off(CanvasEvent.Click, this._bindEventClick)
  }

  private onKeyDown(e) {
    // 
    if (this.editable === Editable.Disabled) return false

    const {keyCode} = e
    const ifDelete = keyCode === 8 || keyCode === 46
    const ifEsc = keyCode === 27

    if (ifEsc) {
      this.updateConfig({
        status: GraphActionStatus.None,
      })
    }

    if (ifDelete) {
      this.focusTarget.forEach((it) => {
        // 
        if (it instanceof Node) {
          const relatedLines = this.dataHandler.nodeRelatedLineMap.get(it.id)
          ;[...relatedLines.from, ...relatedLines.to].map((it) => {
            // 
            const ifReactive = true
            const deleteResults = this.emit(
              LineEvent.BeforeDelete,
              it,
              ifReactive,
            )
            Promise.all(deleteResults)
              .then(() => {
                this.emit(LineEvent.Delete, it, ifReactive)
              })
              .catch(() => warn('Delete abort in [BeforeDelete] hooks.', it))
          })
        }

        const eventName =
          it instanceof Node
            ? NodeEvent
            : it instanceof LineErwin
            ? LineEvent
            : it instanceof NodeRect
            ? MarkEvent
            : it instanceof TextBase
            ? NoteEvent
            : null

        const deleteResults = this.emit(eventName.BeforeDelete, it)
        Promise.all(deleteResults)
          .then(() => {
            this.emit(eventName.Delete, it)
          })
          .catch(() => warn('Delete abort in [BeforeDelete] hooks.', it))
      })

      this.focusTarget.clear()
      this.hoverTarget = null
      this.update()
    }

    this.emit(GraphEvent.Keydown, e)
  }

  private onUndo() {
    const action = this.plugins.get(PluginsEnum.History).undo()
    action?.reverse()
    if (!action) warn('')
  }

  private onRedo() {
    const action = this.plugins.get(PluginsEnum.History).redo()
    action?.reverse()
    if (!action) warn('')
  }

