import {getTextWidth} from '../../../../core/utils/string'
import {
  PADDING,
  FONT_SIZE,
  FONT_FAMILY,
  LINE_HEIGHT,
  DEFAULT_HEIGHT,
  DEFAULT_WIDTH,
  DEFAULT_SUB_WIDTH,
  DEFAULT_SUB_HEIGHT,
  DEFAULT_TITLE_HEIGHT,
} from './conf'

export const getAutoSizeValue = (node) => {
  let width, height
  if (['inclusive', 'exclusive'].includes(node.type)) {
    width = DEFAULT_SUB_WIDTH
    height = DEFAULT_SUB_HEIGHT
  } else {
    width = Math.max(
      Math.floor(
        Math.max(
          ...(node.attributes || []).map((it) =>
            Math.max(
              getTextWidth(it.cnName, FONT_SIZE, FONT_FAMILY),
              getTextWidth(it.enName, FONT_SIZE, FONT_FAMILY),
            ),
          ),
        ) +
          PADDING * 2 +
          FONT_SIZE * 2,
      ),
      DEFAULT_WIDTH,
    )
    const includePrimary =
      (node.attributes || []).findIndex((it) => Boolean(+it.ifPk)) !== -1
    // 
    const placeholderNumber = includePrimary ? 1 : 2
    height = Math.floor(
      Math.max(
        ((node.attributes || []).length + placeholderNumber) * LINE_HEIGHT +
          PADDING * 2,
        DEFAULT_HEIGHT,
      ) + DEFAULT_TITLE_HEIGHT,
    )
  }
  return {
    width,
    height,
  }
}
