/**
 * hexrgb
 * @param {String} hex    hex
 *
 * @return {String} rgb() RGB
 */
export const hexToRgb = function (hex) {
  //#fff#ffffff
  hex.length === 4 &&
    (hex =
      hex.slice(0, 2) +
      hex.slice(1, 2) +
      hex.slice(2, 3) +
      hex.slice(2, 3) +
      hex.slice(3, 4) +
      hex.slice(3, 4))

  return (
    'rgb(' +
    parseInt('0x' + hex.slice(1, 3)) +
    ',' +
    parseInt('0x' + hex.slice(3, 5)) +
    ',' +
    parseInt('0x' + hex.slice(5, 7)) +
    ')'
  )
}

/**
 * 
 * @param {String} color hex/rgb
 *
 * @return {Boolean}     true -> false -> 
 */
export const isLightColor = function (color) {
  const rgbStr = color.includes('rgb') ? color : hexToRgb(color)
  const rgbArr = rgbStr.replace(/rgb\(|rgba\(|\)/g, '').split(',')

  return 0.213 * rgbArr[0] + 0.715 * rgbArr[1] + 0.072 * rgbArr[2] > 255 / 2
}
