import {FabricObject} from '../../shared/fabric'
import {Table, Attribute, CanvasEv, Id} from '../../../types'
import {
  DataTypeDate,
  DataTypeNumber,
  DataTypeString,
  DataTypeBoolean,
  DataTypeBinary,
} from '../../../types/enum'
import {imageMap} from '../../shared/image'
import {isBoolean, isNumber} from '../../utils/verify'
import {isLightColor} from '../../utils/color'
import {uuid} from '../../utils/uuid'
import isString from 'lodash-es/isString'

/**
 * 
 * 1.
 */
interface NodeInput extends Table {
  x?: number
  y?: number
  width?: number
  height?: number
  bgC?: string
  bdC?: string
  quotePrimaryKey?: boolean
  physicalTables?: boolean
  manualChange?: boolean
  ifLogic?: boolean
  ifOnlyLogic?: boolean
  ifOnlyPhy?: boolean
}
export class NodeErwin extends FabricObject {
  private ctx: CanvasRenderingContext2D
  public width = 100
  public height = 100
  public titleHeight = 24
  public fontSize = 12
  private padding = 10

  public attrHeight = 14

  public ifSubEntity = false

  private ifLogic = false
  public ifOnlyLogic = false
  public ifOnlyPhy = false

  public x = 0
  public y = 0
  public id: Id
  public enName: string
  public cnName: string
  public type: string
  public attributes: Attribute[] = []
  public attributesPrimary: Attribute[] = []
  public attributesPrimaryNon: Attribute[] = []
  private expandAttrs = []

  public bdC = '#000000'
  public color = '#000000'
  public bgC = '#FFFFFF'

  private quotePrimaryKey = false
  private physicalTables = false
  private manualChange = false

  constructor(ctx: CanvasRenderingContext2D, input: NodeInput) {
    super()
    this.ctx = ctx
    this.updateOptions(input)
  }

  private analysisOptions(input: NodeInput) {
    if (!input) return

    const {
      x,
      y,
      width,
      height,
      id,
      enName,
      cnName,
      type,
      attributes,
      bgC,
      bdC,
      quotePrimaryKey,
      physicalTables,
      manualChange,
      ifLogic,
      ifOnlyLogic,
      ifOnlyPhy,
    } = input

    if (isBoolean(ifLogic)) this.ifLogic = ifLogic
    if (isBoolean(ifOnlyPhy) || ifOnlyPhy) this.ifOnlyPhy = Boolean(+ifOnlyPhy)
    if (isBoolean(ifOnlyLogic) || ifOnlyLogic)
      this.ifOnlyLogic = Boolean(+ifOnlyLogic)
    if (isNumber(x)) this.x = x
    if (isNumber(y)) this.y = y
    if (id) this.id = id
    if (isString(enName)) this.enName = enName
    if (isString(cnName)) this.cnName = cnName
    if (isNumber(width)) this.width = width
    if (isNumber(height)) this.height = height
    if (type) {
      this.type = type
      this.ifSubEntity = ['exclusive', 'inclusive'].includes(type)
    }
    if (attributes) {
      this.attributes = attributes
      this.attributesPartition()
    }
    if (bgC) this.bgC = bgC
    if (bdC) this.bdC = bdC
    if (isBoolean(quotePrimaryKey)) this.quotePrimaryKey = quotePrimaryKey
    if (isBoolean(physicalTables)) this.physicalTables = physicalTables
    if (isBoolean(manualChange)) this.manualChange = manualChange
  }

  public updateOptions(input: NodeInput) {
    this.analysisOptions(input)
    this.calcBoundingBox()
  }

  public containsPoint(e: CanvasEv) {
    const {x: ex, y: ey} = e
    const {x, y, width: w, height: h, attrHeight, titleHeight, padding} = this
    const targetPointInfo = {
      type: null,
      id: null,
    }

    // 
    const linesForNode = this.getImageLines({
      tl: {x, y: y},
      tr: {x: x + w, y: y},
      bl: {x, y: y + h},
      br: {x: x + w, y: y + h},
    })
    const xpointsNode = this.findCrossPoints(ex, ey, linesForNode)
    const isContainsNode = Boolean(xpointsNode % 2)
    if (!isContainsNode) return targetPointInfo

    // return true
    const ifTitle = ey - y < titleHeight
    if (ifTitle) {
      targetPointInfo.type = 'title'
      return targetPointInfo
    }

    // 
    const attributes = [
      ...this.attributesPrimary,
      {id: 'PrimaryKey'}, // for attr move
      ...this.attributesPrimaryNon,
      {id: 'NonPrimaryKey'}, // for attr move
    ]
    const relativeH =
      ey -
      y -
      titleHeight -
      padding -
      (this.attributesPrimary.length ? 0 : attrHeight)
    const attrIndex = Math.min(
      Math.floor(relativeH / attrHeight),
      attributes.length - 1,
    )
    const attrId = attributes[attrIndex]?.id
    if (attrId) {
      targetPointInfo.type = 'attribute'
      targetPointInfo.id = attrId
    } else {
      targetPointInfo.type = 'entity'
      targetPointInfo.id = this.id
    }
    return targetPointInfo
  }

  public draw(input?: NodeInput) {
    this.updateOptions(input)

    this.drawContainer()
    this.drawTitle()
    this.drawBody()
  }

  private drawContainer() {
    const {
      ctx,
      x,
      y: startY,
      width: w,
      height,
      titleHeight,
      type,
      bgC,
      bdC,
      quotePrimaryKey,
      physicalTables,
      ifSubEntity,
    } = this
    const y = ifSubEntity ? startY : startY + titleHeight
    const h = ifSubEntity ? height : height - titleHeight

    ctx.save()
    ctx.strokeStyle = bdC
    ctx.fillStyle = bgC
    //
    if (ifSubEntity) {
      ctx.beginPath()
      ctx.arc(x + w / 2, y + w / 2, w / 2, 0, Math.PI, true)
      ctx.closePath()
      ctx.fill()
      ctx.stroke()

      if (type === 'exclusive') {
        ctx.moveTo(x + 12, y + 6)
        ctx.lineTo(x + w - 12, y + h - 3)
        ctx.moveTo(x + w - 12, y + 6)
        ctx.lineTo(x + 12, y + h - 3)
        ctx.stroke()
      }
    }
    //
    else {
      const roundedDeg = quotePrimaryKey ? 4 : 0
      ctx.drawRoundedRect(
        x,
        y,
        w,
        h,
        roundedDeg,
        roundedDeg,
        roundedDeg,
        roundedDeg,
        true,
        true,
        physicalTables,
        true,
      )
    }

    // //
    // attributes && this._drawBody(item)

    //
    // if (this.focusTarget.includes(item) && !['exclusive', 'inclusive'].includes(item.type)) this._drawFocus(x, y, w, h)

    ctx.restore()
  }

  private drawDataType(
    datatype:
      | DataTypeDate
      | DataTypeNumber
      | DataTypeString
      | DataTypeBoolean
      | DataTypeBinary,
    x: number,
    y: number,
  ) {
    const datatypeUpper = datatype
      ?.replace(/[^A-Za-z0-9]/g, '')
      ?.toLocaleUpperCase()
    let img
    if (datatypeUpper in DataTypeDate) {
      img = imageMap.get('date')
    } else if (datatypeUpper in DataTypeNumber) {
      img = imageMap.get('number')
    } else if (datatypeUpper in DataTypeString) {
      img = imageMap.get('string')
    } else if (datatypeUpper in DataTypeBoolean) {
      img = imageMap.get('boolean')
    } else if (datatypeUpper in DataTypeBinary) {
      img = imageMap.get('binary')
    }

    const {ctx} = this
    if (!img) {
      ctx.save()
      ctx.fillStyle = '#808080'
      ctx.fillText('?', x, y, this.fontSize)
      ctx.restore()
      return false
    }
    ctx.drawImage(img, x, y, this.fontSize, this.fontSize)
  }

  private drawTitle() {
    const {
      ctx,
      color,
      enName,
      cnName,
      ifLogic,
      width,
      x,
      y,
      titleHeight,
      fontSize,
      ifSubEntity,
    } = this

    ctx.save()
    // 
    ctx.font = `${fontSize}px Arial`
    // 
    ctx.fillStyle = color
    // 
    ctx.textAlign = 'left'
    // 
    ctx.textBaseline = 'top'
    const name = (ifLogic ? cnName : enName) ?? ''
    const fillText = ifSubEntity ? name : ctx.fittingString(name, width - 14)
    const fillX = ifSubEntity ? x + 50 : x
    const fillY = ifSubEntity ? y + 2 : y + 4
    ctx.fillText(fillText, fillX, fillY)
    ctx.restore()
  }

  private drawBody() {
    const {
      ctx,
      x,
      y,
      titleHeight,
      bgC: bgC,
      fontSize,
      padding,
      attrHeight,
      attributesPrimary,
      attributesPrimaryNon,
    } = this

    ctx.save()
    // 
    ctx.font = `${fontSize}px Arial`
    // 
    ctx.fillStyle = isLightColor(bgC) ? '#000' : '#fff'
    // 
    ctx.textAlign = 'left'
    // 
    ctx.textBaseline = 'top'

    const curX = padding + x
    let curY = y + padding - attrHeight + titleHeight

    //
    for (let i = 0; i < attributesPrimary.length; i++) {
      const item = attributesPrimary[i]
      curY = this.drawAttrRow(item, curX, curY)
    }

    ///
    curY = this.drawSplitLine(curY)

    //
    for (let i = 0; i < attributesPrimaryNon.length; i++) {
      const item = attributesPrimaryNon[i]
      curY = this.drawAttrRow(item, curX, curY)
    }

    ctx.restore()
  }

  private drawAttrRow(attrItem, curX, curY) {
    const {
      ctx,
      attrHeight,
      padding,
      fontSize,
      x,
      y,
      width: w,
      height: h,
      ifLogic,
      // attrFocusStyle,
    } = this

    curY += attrHeight

    // 
    if (curY + 16 > h + y) return

    // 
    // if (!this.canvas.isVisible([[curX, curY], [curX + w, curY + fontSize]])) return

    this.drawDataType(attrItem.type, curX, curY)

    // FK
    const addText = Number(attrItem.ifFk) ? '(FK)' : ''
    const attrName = ifLogic ? attrItem.cnName : attrItem.enName
    const finalText = ctx.fittingString(
      attrName + addText,
      w - (curX - x) - padding - fontSize - 4,
    )
    // 
    ctx.fillText(finalText, curX + fontSize + 4, curY)

    // this.drawFocusAttr({
    //   attrItem,
    //   finalText,
    //   curX,
    //   curY,
    //   fullWidth: w - (curX - x),
    //   nodePadding: padding,
    // })

    // icon
    const textWidth = curX + ctx.measureText(finalText).width + fontSize + 4
    this.drawIcons(attrItem.icons, textWidth, curY)

    if (attrItem.children?.length) {
      // 
      const isExpand = this.expandAttrs.includes(attrItem)

      // 
      this.drawTreeExpandArrow.call(this, {
        x: curX,
        y: curY,
        size: fontSize,
        expand: isExpand,
      })

      // 
      isExpand &&
        attrItem.children.forEach((attrItem) => {
          curY = this.drawAttrRow(attrItem, curX + 10, curY)
        })
    }

    return curY
  }

  private drawIcons(icons = [], startX: number, startY: number) {
    const {fontSize, x, width, padding} = this
    for (let i = 0; i < icons.length; i++) {
      const icon = icons[i]
      const iconX = startX + fontSize * 0.7 + i * (fontSize * 1.2)

      if (iconX + (fontSize / 2) * 3 > x + width + padding) return
      this.drawIcon(icon, iconX, startY)
    }
  }

  private drawIcon(icon, startX: number, startY: number) {
    const {ctx, attrHeight, fontSize} = this

    ctx.save()
    /**
     * 
     */
    ctx.beginPath()
    ctx.strokeStyle = icon.bdC || '#0C64EB'
    ctx.fillStyle = icon.bgC || '#ECF5FF'
    ctx.arc(
      startX,
      startY + Math.floor(attrHeight / 2) - 1,
      fontSize / 2,
      0,
      2 * Math.PI,
    )
    ctx.fill()
    ctx.stroke()

    /**
     * 
     */
    ctx.fillStyle = icon.color || '#0C64EB'
    ctx.font = `${Math.ceil(fontSize / 1.2)}px Arial`
    // 
    ctx.textAlign = 'center'
    ctx.fillText(icon.text, startX, startY + (attrHeight - fontSize) / 2 + 1)
    ctx.restore()
  }

  private drawSplitLine(curY) {
    const {ctx, attributesPrimary, attrHeight, x, y, width: w, height: h} = this
    curY += (attributesPrimary.length ? 1 : 2) * attrHeight

    ctx.save()
    const lineY = curY + attrHeight / 3
    if (lineY < h + y) {
      ctx.beginPath()
      ctx.strokeStyle = '#000'
      ctx.moveTo(x, lineY)
      ctx.lineTo(x + w, lineY)
      ctx.stroke()
    }
    ctx.restore()
    return curY
  }

  private drawTreeExpandArrow({x, y, size, expand}) {
    const {ctx} = this

    ctx.save()
    ctx.beginPath()
    ctx.translate(x - 4, y + size / 2)
    expand && ctx.rotate((90 * Math.PI) / 180)
    ctx.moveTo(-2, -4)
    ctx.lineTo(-2, 4)
    ctx.lineTo(2, 0)
    ctx.fill()
    ctx.restore()
  }

  // 
  private attributesPartition() {
    this.attributesPrimary = []
    this.attributesPrimaryNon = []

    const {ifLogic, attributes, attributesPrimary, attributesPrimaryNon} = this

    for (let i = 0; i < attributes.length; i++) {
      const item = attributes[i]
      const ifNotRender =
        (ifLogic && +item.ifPhy) || (!ifLogic && +item.ifLogic)
      if (ifNotRender) continue

      Number(item.ifPk)
        ? attributesPrimary.push(item)
        : attributesPrimaryNon.push(item)
      // id
      if (!item.id) item.id = uuid()
    }
  }

  public getAttributeInfo(attributeId: Id) {
    const {
      attributesPrimary,
      attributesPrimaryNon,
      x,
      y,
      width,
      titleHeight,
      height,
      padding,
      attrHeight,
    } = this
    const attributesOrder = [
      ...attributesPrimary,
      {
        id: 'PrimaryKey',
        ifPk: true,
        enName: '',
        cnName: '',
        type: null,
        ifFk: false,
        icons: [],
      },
      ...attributesPrimaryNon,
      {
        id: 'NonPrimaryKey',
        ifPk: false,
        enName: '',
        cnName: '',
        type: null,
        ifFk: false,
        icons: [],
      },
    ]
    const attrIndex =
      attributesOrder.findIndex((i) => i.id === attributeId) ??
      attributesOrder.length - 1

    const info = {
      x: x + padding,
      y:
        y +
        padding +
        titleHeight +
        (attributesPrimary.length ? attrIndex : attrIndex + 1) * attrHeight,
      width: width - 2 * padding,
      height: attrHeight,
      info: attributesOrder[attrIndex],
      padding: padding,
    }
    // console.log(this,'-------------------')
    return info
  }

  private calcBoundingBox() {
    const {x, y, width, height} = this

    this.boundingBox = [
      [x, y],
      [x + width, y],
      [x, y + height],
      [x + width, y + height],
    ]
  }
}
